.navbar-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50px;
    background-color: rgba(0,0,0, 0.8);
    z-index: 1000;
}
.home-container .navbar-container{
    top: 0;
    position: relative;
    background: none;
}

.navbar-container li{
    width: 50vw;
    height: 50%;
    font-size: 1.5rem;
    margin: 0.5rem ;
    padding: 0.1rem 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    /* background-color: antiquewhite; */
    border-radius: 10px;
    box-shadow: 2px 2px 5px 2px rgba(0,0,0, 0.3);
    text-align: center;
    background-color: antiquewhite;

    color: #000;    
    text-decoration: none;
    animation: fadeInLeft 0.5s;
}
@keyframes fadeInLeft{
    0%{
        opacity: 0;
        transform: translateX(-20px);
    }
    100%{
        opacity: 1;
        transform: translateX(0px);
    }
}

@media screen and (min-width: 768px) {
    .navbar-container{
        flex-direction: row;
        justify-content: flex-end;
        position: relative;
        top: 0;
        background-color: transparent;
        margin-right: 0;
    }
    .navbar-container li{
        width: auto;
        height: 50%;
        
        /* background-color: antiquewhite; */
        border-radius: 10px;
    }
}